.MyDcBzj-container {
  position: fixed;
  top: 0%;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 200;
  background: rgba(0, 0, 0, 0.5) !important;
  /* 兼容ie几不知道，好像ie5 */
  background: #000;
  filter: alpha(opacity=60);
}
/*设置div-child的父元素主要是将要此元素的父元素透明特性继承过来，故div-child不会半透明，而是不透明，解决了父元素透明，子元素也透明的bug */
.MyDcBzj-child-container {
  position: relative;
  width: 56%;
  height: 62%;
  margin: auto;
  top: 20%;
  z-index: 250;
  /*z-index要放在父元素之上 */
  background-size: contain;
  background-repeat: no-repeat;
}
.MyDcBzj-child {
  position: relative;
  top: 20%;
  width: 90%;
  height: 45%;
  box-shadow: 0 5px 4px 0px #8195bb;
  border-radius: 10px;
  margin: auto;
  z-index: 300;
  /*z-index要放在父元素之上 */
  text-align: center;
  background: #eff3f6;
}
.MyDcBzj-child :nth-child(1) {
  position: relative;
  width: 8%;
  height: 100%;
  margin-left: 13%;
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  float: left;
}
.MyDcBzj-child .MyDcBzj-child-child2 {
  width: 75%;
  height: 50%;
  float: left;
}
.MyDcBzj-child .MyDcBzj-child-child2 :nth-child(1) {
  float: left;
  position: relative;
  width: 30%;
  height: 50%;
  margin: auto;
  margin-left: 5%;
  margin-top: 10%;
}
.MyDcBzj-child .MyDcBzj-child-child2 :nth-child(2) {
  float: left;
  position: relative;
  width: 40%;
  height: 60%;
  margin: auto;
  margin-top: 8%;
  background-position: center;
  margin-left: 1%;
  background-size: contain;
  background-repeat: no-repeat;
  text-align: start;
  line-height: 100%;
}
.MyDcBzj-child .MyDcBzj-child-child2 :nth-child(2) input {
  width: 70%;
  height: 90%;
  position: relative;
  left: 10%;
  margin-top: 0;
  background: transparent;
  border: transparent;
  outline: none;
  font-size: 14px;
  color: white;
}
.MyDcBzj-child .MyDcBzj-child-child2 :nth-child(3) {
  float: left;
  position: relative;
  width: 30%;
  height: 50%;
  margin: auto;
  margin-left: 5%;
  margin-top: 5%;
}
.MyDcBzj-child .MyDcBzj-child-child2 :nth-child(4) {
  float: left;
  position: relative;
  width: 40%;
  height: 60%;
  margin: auto;
  margin-top: 4%;
  background-position: center;
  margin-left: 1%;
  background-size: contain;
  background-repeat: no-repeat;
  text-align: start;
  line-height: 100%;
}
.MyDcBzj-child .MyDcBzj-child-child2 :nth-child(4) input {
  width: 70%;
  height: 90%;
  position: relative;
  left: 10%;
  margin-top: 0;
  background: transparent;
  border: transparent;
  outline: none;
  font-size: 14px;
  color: white;
}
.MyDcBzj-btn1 {
  width: 90%;
