.list-container {
  position: fixed;
  top: 0%;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 200;
  background: rgba(0, 0, 0, 0.5) !important;
  /* 兼容ie几不知道，好像ie5 */
  background: #000;
  filter: alpha(opacity=60);
}
/*设置div-child的父元素主要是将要此元素的父元素透明特性继承过来，故div-child不会半透明，而是不透明，解决了父元素透明，子元素也透明的bug */
.list-child-container {
  position: relative;
  width: 56%;
  height: 60%;
  margin: auto;
  top: 20%;
  z-index: 250;
  /*z-index要放在父元素之上 */
  background-size: contain;
  background-repeat: no-repeat;
}
.list-child {
  position: relative;
  top: 20%;
  width: 90%;
  height: 48%;
  box-shadow: 0 5px 4px 0px #8195bb;
  border-radius: 10px;
  margin: auto;
  z-index: 300;
  /*z-index要放在父元素之上 */
  text-align: center;
  background: #eff3f6;
}
.list-child :nth-child(1) {
  position: relative;
  width: 10%;
  height: 100%;
  margin-left: 5%;
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  float: left;
}
.list-child :nth-child(2) {
  float: left;
  position: relative;
  top: 5%;
  width: 80%;
  height: 80%;
  margin: auto;
  font-size: 12px;
  color: #e54e34;
  padding-left: 5%;
}
.list-child :nth-child(2) :nth-child(1) {
  margin: 0;
  font-size: 12px;
  width: auto;
  height: 20%;
  top: 0;
  position: absolute;
}
.list-child :nth-child(2) :nth-child(2) {
  font-size: 12px;
  margin: 0;
  padding: 0;
  width: auto;
  height: 20%;
  top: 25%;
  position: absolute;
}
.list-child :nth-child(2) :nth-child(3) {
  font-size: 12px;
  margin: 0;
  padding: 0;
  width: auto;
  height: 20%;
  top: 50%;
  position: absolute;
}
.list-child :nth-child(2) :nth-child(4) {
  font-size: 12px;
  margin: 0;
  padding: 0;
  width: auto;
  height: 40%;
  top: 75%;
  position: absolute;
  text-align: start;
}
.list-btn1 {
  width: 90%;
  height: 16%;
  position: relative;
  top: 24%;
  margin: auto;
  text-align: center;
}
.list-btn1 :nth-child(1) {
  margin-left: 20%;
  width: 20%;
  height: 100%;
  background-size: contain;
  background-repeat: no-repeat;
  float: left;
  cursor: pointer;
}
.list-btn1 :nth-child(2) {
  margin-left: 20%;
  width: 20%;
  height: 100%;
  background-size: contain;
  background-repeat: no-repeat;
  float: left;
  cursor: pointer;
}
