.Applyfor-container {
  position: fixed;
  top: 0%;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 200;
  background: rgba(0, 0, 0, 0.5) !important;
  /* 兼容ie几不知道，好像ie5 */
  background: #000;
  filter: alpha(opacity=60);
}
/*设置div-child的父元素主要是将要此元素的父元素透明特性继承过来，故div-child不会半透明，而是不透明，解决了父元素透明，子元素也透明的bug */
.Applyfor-child-container {
  position: relative;
  width: 53%;
  height: 91%;
  margin: auto;
  top: 2%;
  z-index: 250;
  /*z-index要放在父元素之上 */
  background-size: contain;
  background-repeat: no-repeat;
}
.Applyfor-title {
  position: relative;
  width: 30%;
  height: 10%;
  top: 2%;
  margin: auto;
  background-size: contain;
  background-repeat: no-repeat;
}
.Applyfor-child {
  position: relative;
  top: 4%;
  left: 5%;
  width: 90%;
  height: 53%;
  box-shadow: 0 5px 4px 0px #8195bb;
  border-radius: 10px;
  float: left;
  z-index: 350;
  /*z-index要放在父元素之上 */
  text-align: center;
  background: #eff3f6;
}
.Applyfor-child div {
  width: 90%;
  margin: auto;
  height: 15%;
  position: relative;
  text-align: start;
  color: #474a77;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: right;
}
.Applyfor-child div img {
  width: 20%;
  height: 100%;
  position: absolute;
  bottom: 0;
  right: 0;
}
.Applyfor-child :nth-child(1) {
  margin-top: 2%;
}
.Applyfor-child :nth-child(1) span {
  margin-top: 0;
  width: 50%;
  height: 100%;
  position: relative;
  float: left;
}
.Applyfor-child :nth-child(6) {
  height: 12%;
  width: 115%;
  margin: auto;
  position: absolute;
  left: -7%;
  font-size: 10px;
  transform: scale(0.8);
  color: #af4343;
}
