.RechargeBox {
  height: 100%;
  width: 100%;
  background-size: contain;
  background-repeat: no-repeat;
}
.RechargeBox .WordArt {
  font-size: 16px;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: bolder;
  background-image: -webkit-gradient(linear, 0 0, 0 bottom, from(#74e4ec), to(#5263c6));
}
.RechargeBox .publicbtn {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  color: #bc6414;
  font-weight: bold;
}
.RechargeBox .publicbtn span {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-bottom: 6%;
}
.RechargeBox .Alert-container {
  position: fixed;
  top: 0%;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 200;
  background: rgba(0, 0, 0, 0) !important;
  /* 兼容ie几不知道，好像ie5 */
  filter: alpha(opacity=0);
}
.RechargeBox .Alert-child-container {
  position: relative;
  width: 30%;
  height: 14%;
  margin: auto;
  top: 40%;
  z-index: 250;
  /*z-index要放在父元素之上 */
  background-size: contain;
  background-repeat: no-repeat;
  background: #1e1531;
  display: flex;
  align-items: center;
  padding-top: 0;
  border-radius: 5px ;
  opacity: 0.9;
}
.RechargeBox .Alert-child {
  position: relative;
  top: 0%;
  width: 55%;
  margin-left: 5%;
  height: 100%;
  border-radius: 5px;
  float: left;
  z-index: 300;
  /*z-index要放在父元素之上 */
  text-align: center;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
}
.RechargeBox .Alert-btn {
  float: left;
  width: 35%;
  height: 50%;
  position: relative;
  text-align: center;
}
.RechargeBox .Alert-btn :nth-child(1) {
  width: 100%;
  height: 100%;
  background-size: contain;
  background-repeat: no-repeat;
  float: left;
  background: #0098ff;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
}
.RechargeBox .RechargeBox_child1 {
  height: 15.7%;
  width: 100%;
  position: relative;
}
.RechargeBox .RechargeBox_child1 :nth-child(1) {
  position: absolute;
  left: 1%;
  top: 10%;
  width: 6%;
  height: 75%;
  background-size: contain;
  background-repeat: no-repeat;
}
.RechargeBox .RechargeBox_child1 .child_title {
  width: 32.8%;
  height: 88%;
  background-size: contain;
  background-repeat: no-repeat;
  margin: 0 auto;
}
.RechargeBox .RechargeBox_child1 .child_title :nth-child(1) {
  position: relative;
  width: 24%;
  height: 70%;
  margin: auto;
  background-size: contain;
  background-repeat: no-repeat;
}
.RechargeBox .RechargeBox_child1 .historyBtn {
  position: absolute;
  right: 1%;
  top: 20%;
  width: 10%;
  height: 45%;
  background-size: contain;
  background-repeat: no-repeat;
}
.RechargeBox .RechargeBox_child2 {
  position: relative;
  width: 100%;
  height: 81%;
  padding-top: 1%;
  box-sizing: border-box;
}
.RechargeBox .RechargeBox_child2 .child2_nav {
  position: relative;
  float: left;
  width: 17.6%;
  height: 87%;
  padding-top: 0%;
}
.RechargeBox .RechargeBox_child2 .child2_nav div {
  position: relative;
  height: 18%;
  width: 85%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-size: contain;
  background-repeat: no-repeat;
  margin-top: 5%;
  color: #d9f8fb;
  font-weight: bolder;
  font-size: 14px;
}
.RechargeBox .RechargeBox_child2 .child2_nav .cur {
  transform: scale(1.16, 1);
  transform-origin: left center;
  background-repeat: no-repeat;
  color: #bb5b02;
}
.RechargeBox .RechargeBox_child2 .child2_nav .cur span {
  margin-right: 10%;
}
.RechargeBox .RechargeBox_child2 .child2_nav :nth-child(3) {
  font-size: 12px;
}
.RechargeBox .RechargeBox_child2 .child_content {
  position: relative;
  float: left;
  width: 80.5%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  filter: alpha(opacity=60);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
}
.RechargeBox .RechargeBox_child2 .child_content .inner_box {
  position: relative;
  background: #d9f8fb;
  border-radius: 7px;
  width: 97.8%;
  height: 96%;
  opacity: 1;
  box-sizing: border-box;
  padding: 2%;
  box-shadow: 5px 0 2px #bbd6d9 inset, -5px 0 2px #bbd6d9 inset, 0px 5px 2px #bbd6d9 inset, 5px -5px 2px #bbd6d9 inset;
}
.Alert2-title {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 17%;
}
.Alert2-title :nth-child(1) {
  width: 27%;
  margin: 0 auto;
  height: 100%;
  font-size: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #115cb5;
  font-weight: bold;
}
.Alert2-title :nth-child(2) {
  position: absolute;
  right: 2%;
  top: 0%;
  width: 5%;
  font-size: 20px;
  color: #115cb5;
}
